import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from "@/views/HomeView";
import TestView from "@/views/TestView";
import PersonalView from "@/views/PersonalView";
import ResumeView from "@/views/ResumeView";
import CommunityView from "@/views/CommunityView";
import Login from "@/components/login/Login";
import errorPage from "@/components/errorPage";

Vue.use(VueRouter)

const routes = [
  {path: '/home', name: 'home',
    meta: {
      requireAuth: true
    },
    component: HomeView},
  {path: '/', redirect:'/home',},
  {path: '/test',
    meta: {
      requireAuth: true
    },
    component: TestView,},
  {path: '/personal',
    meta: {
      requireAuth: true
    },
    component: PersonalView},
  {path: '/resume',
    meta: {
      requireAuth: true
    },
    component: ResumeView},
  {path: '/community',
    meta: {
      requireAuth: true
    },
    component: CommunityView},
  {path: '/login',name:'login', component: Login},
  {path: '/404', name:'error', component: errorPage},
  {path: '*', redirect:'/404',},
]


const router = new VueRouter({
  routes,
  // mode: "history",
  // base: process.env.BASE_URL,
})
router.beforeEach((to, from, next) => {
  const isLogin = window.sessionStorage.getItem('userId'); //获取本地存储的登陆信息
  console.log(isLogin)
  console.log("to:"+to.name) //进入到哪个路由去
  console.log("from:"+from.name) //从哪个路由离开
  //next：路由的控制参数，常用的有next(true)和next(false)
  //next() 直接进to 所指路由
  //next('route') 跳转指定路由

  if (to.meta.requireAuth) {//判断当前页面是否为拦截页面 如果是的话：
    if (isLogin) {  //判断是否登陆
      next();  //已登录，跳转首页
    } else {
      next({name:'login'});  //没登录，继续进入login页
    }
  }else{//如果不是要拦截页面，就放行。
    next();
  }

});

export default router
