<template>
  <div>
<!--  <h1 class="title">证书墙</h1>-->
  <div class="container">
    <div v-for="item in cards" :key="item.id" @click="showImg(item.image)">
    <card :data-image="item.image" >
      <h1 slot="header">{{item.title}}</h1>
      <p slot="content">{{item.content}}</p>
    </card>
    </div>
  </div>
    <el-image-viewer v-if="showViewer"
                     :on-close="closeImgViewer"  :url-list="[viewUrl]"/>
  </div>
</template>

<script>
import card from "@/components/community/ImportCards";
import ElImageViewer from 'element-ui/packages/image/src/image-viewer';
export default {
  name: "HomeHoverCards",
  components:{
    card,
    ElImageViewer,
  },
  data() {
    return {
      viewUrl:"",
      showViewer:"",
      cards:[
        {
          id:1,
          image: "https://img0.baidu.com/it/u=3213146515,249228945&fm=253&fmt=auto&app=138&f=JPEG?w=640&h=452",
          title:"优秀组织奖",
          content:"这里是优秀组织奖的说明",
        },
        {
          id:2,
          image: "https://img2.baidu.com/it/u=3876084319,1032665946&fm=253&fmt=auto&app=138&f=JPEG?w=500&h=355",
          title:"志愿者证书",
          content:"全国大学生算法设计与编程挑战赛证书",
        },
        {
          id:3,
          image: "https://img.51miz.com/Element/00/82/14/34/3e80cec9_E821434_81b1fa70.png",
          title:"英语证书",
          content:"英语证书英语证书英语证书的说明",
        },
        {
          id:4,
          image: "https://ts1.cn.mm.bing.net/th/id/R-C.917286bb8bb37dd417a020e52d12f89a?rik=2B1RLw53uDLg6A&riu=http%3a%2f%2fwww.hnscjjc.com%2fimages%2fimage%2f20170325104869586958.jpg&ehk=3%2b1wG30ccnqv7buPq0R%2bLcJweH43iN7OlynJr5h1jYw%3d&risl=&pid=ImgRaw&r=0",
          title:"优秀单位",
          content:"说明",
        },
        {
          id:5,
          image: "https://ts1.cn.mm.bing.net/th/id/R-C.27b71dc12e5674824dcc8d66cc25332b?rik=maCgSNS2V7LLOw&riu=http%3a%2f%2f5b0988e595225.cdn.sohucs.com%2fimages%2f20171206%2fbed0764c049a4417992a355663f42779.jpeg&ehk=3b7ts2oCyihXLFFXsvLu2CmlqLEzUVCIcBnLhknj%2fAo%3d&risl=&pid=ImgRaw&r=0",
          title:"计算机二级",
          content:"参加2019年3月java等级考试",
        },
        {
          id:6,
          image: "https://img1.baidu.com/it/u=1037107274,3792195980&fm=253&fmt=auto&app=120&f=JPEG?w=500&h=708",
          title:"学生综合素质证书",
          content:"对大学生综合素质唯一的官方认证模式。",
        }
      ]
    }
  },
  methods:{
    showImg(url){
      this.showViewer=true
      this.viewUrl=url
      const m = (e) => { e.preventDefault() };
      document.body.style.overflow = 'hidden';
      document.addEventListener("touchmove", m, false); // 禁止页面滑动
        // window.open(url,'_blank')
    },
    closeImgViewer(){
      this.showViewer=false
      this.imgViewerVisible = false;
      const m = (e) => { e.preventDefault() };
      document.body.style.overflow = 'auto';
      document.removeEventListener("touchmove", m, true);
    },
  },
}



</script>

<style scoped>
body {
  margin: 40px 0;
  font-family: "Raleway";
  font-size: 14px;
  font-weight: 500;
  background-size: 100%;
}

.title {
  font-family: "Raleway";
  font-size: 24px;
  font-weight: 700;
  color: #5D4037;
  text-align: center;
}

p {
  line-height: 1.5em;
}

h1 + p, p + p {
  margin-top: 10px;
}

.container {
  padding: 40px 80px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.card-wrap:hover .card-info {
  transform: translateY(0);
}
.card-wrap:hover .card-info p {
  opacity: 1;
}

.card-info p {
  opacity: 0;
  text-shadow: black 0 2px 3px;
  transition: 0.6s 1.6s cubic-bezier(0.215, 0.61, 0.355, 1);
}
.card-info * {
  position: relative;
  z-index: 1;
}

.card-info h1 {
  font-family: "Playfair Display";
  font-size: 30px;
  font-weight: 700;
  text-shadow: rgba(0, 0, 0, 0.5) 0 10px 10px;
}
</style>