<template>
      <el-card shadow="always" class="my_stu">
        <div ref="chart"  style="width: 1200px;height:600px;"></div>
      </el-card>
</template>

<script>
const echarts = require('echarts');
export default {
  name: "StudentsEcharts",
  data(){
    return{
      activeName:"first",
    }
  },
  methods:{
    initCharts () {
      let myChart = echarts.init(this.$refs.chart);
      myChart.setOption({
        legend: {},
        tooltip: {
          trigger: 'axis',
              showContent: false
        },
        dataset: {
          source: [
            // ['product', '2012', '2013', '2014', '2015', '2016', '2017'],
            ['product', "2020第一学期","2020第二学期","2021第一学期","2021第二学期","2022第一学期","2022第二学期"],
            ["学科竞赛", 56.5, 82.1, 88.7, 70.1, 53.4, 85.1],
            ["资格证书", 51.1, 51.4, 55.1, 53.3, 73.8, 68.7],
            ["项目实践", 40.1, 62.2, 69.5, 36.4, 45.2, 32.5],
            ["其他", 25.2, 37.1, 41.2, 18, 33.9, 49.1]
          ]
        },
        xAxis: { type: 'category' },
        yAxis: { gridIndex: 0 },
        grid: { top: '55%' },
        series: [
          {
            type: 'line',
            smooth: true,
            seriesLayoutBy: 'row',
            emphasis: { focus: 'series' }
          },
          {
            type: 'line',
            smooth: true,
            seriesLayoutBy: 'row',
            emphasis: { focus: 'series' }
          },
          {
            type: 'line',
            smooth: true,
            seriesLayoutBy: 'row',
            emphasis: { focus: 'series' }
          },
          {
            type: 'line',
            smooth: true,
            seriesLayoutBy: 'row',
            emphasis: { focus: 'series' }
          },
          {
            type: 'pie',
            id: 'pie',
            radius: '30%',
            center: ['50%', '25%'],
            emphasis: {
              focus: 'self'
            },
            label: {
              formatter: '{b}: {@2020第一学期} ({d}%)'
            },
            encode: {
              itemName: 'product',
              value: '2020第一学期',
              tooltip: '2020第一学期'
            }
          }
        ]
      })
      myChart.on('updateAxisPointer', function (event) {
        const xAxisInfo = event.axesInfo[0];
        if (xAxisInfo) {
          const dimension = xAxisInfo.value + 1;
          myChart.setOption({
            series: {
              id: 'pie',
              label: {
                formatter: '{b}: {@[' + dimension + ']} ({d}%)'
              },
              encode: {
                value: dimension,
                tooltip: dimension
              }
            }
          });
        }
      });
    }
  },
  mounted() {
    this.initCharts();
  }
}
</script>

<style scoped>
@media (max-width: 500px)  {
  .my_stu{
    display: none;
  }
}
</style>
